import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../StoreManager/StoreManager.module.css";

const SelectHeadCard = ({ head, selectedId, onSelect }) => {
  const { t } = useTranslation();
  const onClick = () => {
    onSelect(head);
  };
  return (
    <div
      className={
        selectedId?.includes(head?._id)
          ? `${style.smc_card} ${style.smc_card_active}`
          : style.smc_card
      }
      onClick={onClick}
    >
      <div className={style.smc_card_contatiner}>
        <img src={`${assestsUserFinanceUrl}/category.svg`} alt="categor icon" />
        <div className={style.smc_card_contatiner_inner}>
          <h6>
            {t("name")}
            {": "}
            {head?.heads_name ?? ""}
          </h6>
          {head?.heads_parent ? (
            <p>
              {t("head_type")} {": "} {head?.heads_parent}
            </p>
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default SelectHeadCard;
