import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import style from "../../../StoreManager/StoreManager.module.css";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import SalaryStructurePreview from "./SalaryStructurePreview";

const SalaryStructureCard = ({ struct }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.smc_card} onClick={onToggle}>
        <div className={style.smc_card_contatiner}>
          <img
            src={`${assestsUserFinanceUrl}/category.svg`}
            alt="categor icon"
          />
          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {t("structure_type")} {": "}
              {struct?.structure_status === "ON_GRANT" ? "Grant" : "Non-Grant"}
            </h6>
            <p>
              {t("date")} {": "}{" "}
              {dayjs(struct?.created_at).format("DD/MM/YYYY")}
            </p>
          </div>
        </div>
      </div>

      {open && <SalaryStructurePreview onClose={onToggle} ssid={struct?._id} />}
    </>
  );
};

export default SalaryStructureCard;
