import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { usePayrollAllSalaryHead } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../../Ui/Button/CreateButton";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import SelectHeadCard from "./SelectHeadCard";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../PlateformUser/PlateformUser.module.css";
const SelectSalaryComponent = ({
  onClose,
  pid,
  dataState,
  onGrabSalary,
  isSegment,
  dataStateId,
  title,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [selectedId, setSelectedId] = useState([]);
  const [selectedObj, setSelectedObj] = useState([]);
  const { payrollAllSalaryHead, payrollAllSalaryHeadLoading } =
    usePayrollAllSalaryHead({
      data: {
        pid: pid,
        page: page,
        limit: 10,
        search: search,
        type: isSegment,
      },
      skip: pid ? !isSegment : !pid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (payrollAllSalaryHead?.list?.length) {
      let obj = nextPage(payrollAllSalaryHead?.list?.length);
      setState(() => obj);
    }
  }, [payrollAllSalaryHead?.list?.length]);

  useEffect(() => {
    setSelectedId(dataStateId);
    setSelectedObj(dataState);
  }, [dataStateId, dataState]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onSelect = (val) => {
    const objProperties = {
      head_name: val?.heads_name,
      head_amount: +val?.heads_price,
      master: val?._id,
    };

    if (selectedId?.includes(val?._id)) {
      setSelectedId((prevState) => prevState?.filter((id) => id !== val?._id));
      setSelectedObj((prevState) =>
        prevState?.filter((dt) => dt?.master !== val?._id)
      );
      // let selectedIndex = selectedId?.indexOf(val?._id);
      // let selectedFeeItem = [];
      // for (let i = 0; i < selectedFee?.length; i++) {
      //   if (i === selectedIndex) {
      //   } else {
      //     selectedFeeItem.push(selectedFee[i]);
      //   }
      // }
      // setSelectedFeeId((prevState) =>
      //   prevState?.filter((obj) => obj !== val?._id)
      // );
      // setSelectedFee(selectedFeeItem);
    } else {
      setSelectedId((prevState) => [...prevState, val?._id]);
      setSelectedObj((prevState) => [...prevState, objProperties]);
    }
  };

  const onDone = () => {
    onGrabSalary(selectedObj, selectedId);
    onClose();
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(title ?? "select_salary_component")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              <CreateButton
                label={t("select")}
                count={selectedId?.length}
                onAction={onDone}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 0.5rem",
                }}
              />
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {payrollAllSalaryHead?.list?.map((head, index) =>
            payrollAllSalaryHead?.list?.length === index + 1 ? (
              <div key={head?._id} ref={ref}>
                <SelectHeadCard
                  head={head}
                  selectedId={selectedId}
                  onSelect={onSelect}
                />
              </div>
            ) : (
              <SelectHeadCard
                key={head?._id}
                head={head}
                selectedId={selectedId}
                onSelect={onSelect}
              />
            )
          )}
          {payrollAllSalaryHeadLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            payrollAllSalaryHead?.list?.length === 0 && (
              <EmptyMessage
                title="No heads found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            payrollAllSalaryHead?.list?.length === 0 && (
              <EmptyMessage
                title="No heads."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectSalaryComponent;
