import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import { useEffect, useState } from "react";
import { usePayrollAddSalaryStructure } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import SelectSalaryComponent from "./SelectSalaryComponent";
const AddSalaryStructure = ({ onClose, onRefetch, pid, flow }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    sal_com: [],
    employee_ded: [],
    employar_ded: [],
    com: [],
  });
  const [stateId, setStateId] = useState({
    sal_com: [],
    employee_ded: [],
    employar_ded: [],
    com: [],
  });
  const [stateTotal, setStateTotal] = useState({
    sal_com: 0,
    employee_ded: 0,
    employar_ded: 0,
    com: 0,
  });
  const [step, setStep] = useState("");
  const [payrollAddSalaryStructure] = usePayrollAddSalaryStructure();

  useEffect(() => {
    if (state.sal_com?.length) {
      let tp = 0;
      for (let st of state.sal_com) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        sal_com: tp,
      }));
    }

    if (state.employee_ded?.length) {
      let tp = 0;
      for (let st of state.employee_ded) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        employee_ded: tp,
      }));
    }

    if (state.employar_ded?.length) {
      let tp = 0;
      for (let st of state.employar_ded) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        employar_ded: tp,
      }));
    }

    if (state.com?.length) {
      let tp = 0;
      for (let st of state.com) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        com: tp,
      }));
    }
  }, [state.sal_com, state.employar_ded, state.employee_ded, state.com]);
  const onSalaryComponent = (dataObj, data) => {
    setState((prev) => ({
      ...prev,
      sal_com: dataObj,
    }));
    setStateId((prev) => ({
      ...prev,
      sal_com: data,
    }));
    let tp = 0;
    for (let st of dataObj) {
      tp += st?.head_amount;
    }
    setStateTotal((prev) => ({
      ...prev,
      sal_com: tp,
    }));
  };

  const onEmployee = (dataObj, data) => {
    setState((prev) => ({
      ...prev,
      employee_ded: dataObj,
    }));
    setStateId((prev) => ({
      ...prev,
      employee_ded: data,
    }));
    let tp = 0;
    for (let st of dataObj) {
      tp += st?.head_amount;
    }
    setStateTotal((prev) => ({
      ...prev,
      employee_ded: tp,
    }));
  };

  const onEmployer = (dataObj, data) => {
    setState((prev) => ({
      ...prev,
      employar_ded: dataObj,
    }));
    setStateId((prev) => ({
      ...prev,
      employar_ded: data,
    }));
    let tp = 0;
    for (let st of dataObj) {
      tp += st?.head_amount;
    }
    setStateTotal((prev) => ({
      ...prev,
      employar_ded: tp,
    }));
  };

  const onCompliances = (dataObj, data) => {
    setState((prev) => ({
      ...prev,
      com: dataObj,
    }));
    setStateId((prev) => ({
      ...prev,
      com: data,
    }));
    let tp = 0;
    for (let st of dataObj) {
      tp += st?.head_amount;
    }
    setStateTotal((prev) => ({
      ...prev,
      com: tp,
    }));
  };
  const onFeeHeadPriceChange = (e, index, flow) => {
    if (flow === "SALARY_COMPONENTS") {
      const input1 = [...state.sal_com];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        sal_com: input1,
      }));
    } else if (flow === "EMPLOYEE_DEDUCTION") {
      const input1 = [...state.employee_ded];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        employee_ded: input1,
      }));
    } else if (flow === "EMPLOYAR_DEDUCTION") {
      const input1 = [...state.employar_ded];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        employar_ded: input1,
      }));
    } else if (flow === "COMPLIANCES") {
      const input1 = [...state.com];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        com: input1,
      }));
    } else {
    }
  };

  const onFeeHeadLabelChange = (e, index, flow) => {
    if (flow === "SALARY_COMPONENTS") {
      const input1 = [...state.sal_com];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_name = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        sal_com: input1,
      }));
    } else if (flow === "EMPLOYEE_DEDUCTION") {
      const input1 = [...state.employee_ded];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_name = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        employee_ded: input1,
      }));
    } else if (flow === "EMPLOYAR_DEDUCTION") {
      const input1 = [...state.employar_ded];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_name = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        employar_ded: input1,
      }));
    } else if (flow === "COMPLIANCES") {
      const input1 = [...state.com];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_name = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        com: input1,
      }));
    } else {
    }
  };

  const onAddHeadPerson = () => {
    if (pid && flow) {
      setDisabled((pre) => !pre);
      payrollAddSalaryStructure({
        pid: pid,
        bodyParameter: {
          ...state,
          status: flow,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === "SALARY_COMPONENTS" ? (
        <SelectSalaryComponent
          pid={pid}
          isSegment="SALARY_COMPONENTS"
          onClose={() => setStep("")}
          dataState={state.sal_com}
          onGrabSalary={onSalaryComponent}
          dataStateId={stateId.sal_com}
        />
      ) : step === "EMPLOYEE_DEDUCTION" ? (
        <SelectSalaryComponent
          pid={pid}
          isSegment="EMPLOYEE_DEDUCTION"
          onClose={() => setStep("")}
          dataState={state.employee_ded}
          dataStateId={stateId.employee_ded}
          onGrabSalary={onEmployee}
          title="select_employee_deduction"
        />
      ) : step === "EMPLOYAR_DEDUCTION" ? (
        <SelectSalaryComponent
          pid={pid}
          isSegment="EMPLOYAR_DEDUCTION"
          onClose={() => setStep("")}
          dataState={state.employar_ded}
          onGrabSalary={onEmployer}
          dataStateId={stateId.employar_ded}
          title="select_employer_deduction"
        />
      ) : step === "COMPLIANCES" ? (
        <SelectSalaryComponent
          pid={pid}
          isSegment="COMPLIANCES"
          onClose={() => setStep("")}
          dataState={state.com}
          onGrabSalary={onCompliances}
          dataStateId={stateId.com}
          title="select_compliances"
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_salary_structure")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("salary_component")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {state.sal_com?.map((afh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <input
                        type={"text"}
                        value={afh?.head_name}
                        onChange={(e) =>
                          onFeeHeadLabelChange(e, index, "SALARY_COMPONENTS")
                        }
                        className={style.fee_head_name_input}
                      />
                      <input
                        type={"number"}
                        value={afh?.head_amount}
                        onChange={(e) =>
                          onFeeHeadPriceChange(e, index, "SALARY_COMPONENTS")
                        }
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <button
                  className={style.add_fees_head_btn}
                  title="Add Fees"
                  onClick={() => setStep("SALARY_COMPONENTS")}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  {t("add")}
                  <img src={`${assestsUserFinanceUrl}/plus.svg`} alt="add" />
                </button>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                      borderBottom: "none",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("total_earning")}</h6>
                    <h6
                      className={style.fees_total}
                      style={{
                        fontWeight: "700",
                      }}
                    >
                      {stateTotal.sal_com}
                    </h6>
                  </div>
                </div>
              </section>

              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("employee_deduction")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {state.employee_ded?.map((gfh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <input
                        type={"text"}
                        value={gfh?.head_name}
                        onChange={(e) =>
                          onFeeHeadLabelChange(e, index, "EMPLOYEE_DEDUCTION")
                        }
                        className={style.fee_head_name_input}
                      />
                      <input
                        type={"number"}
                        value={gfh?.head_amount}
                        onChange={(e) =>
                          onFeeHeadPriceChange(e, index, "EMPLOYEE_DEDUCTION")
                        }
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <button
                  className={style.add_fees_head_btn}
                  title="Add Fees"
                  onClick={() => setStep("EMPLOYEE_DEDUCTION")}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  {t("add")}
                  <img src={`${assestsUserFinanceUrl}/plus.svg`} alt="add" />
                </button>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                      borderBottomColor: "rgba(18, 18, 18, 0.8)",
                      borderBottomWidth: "2px",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("total_pay")}</h6>
                    <h6
                      className={style.fees_total}
                      style={{
                        fontWeight: "700",
                      }}
                    >
                      {stateTotal.sal_com - stateTotal.employee_ded}
                    </h6>
                  </div>
                </div>
              </section>
              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("compliances")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {state.com?.map((gfh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <input
                        type={"text"}
                        value={gfh?.head_name}
                        onChange={(e) =>
                          onFeeHeadLabelChange(e, index, "COMPLIANCES")
                        }
                        className={style.fee_head_name_input}
                      />
                      <input
                        type={"number"}
                        value={gfh?.head_amount}
                        onChange={(e) =>
                          onFeeHeadPriceChange(e, index, "COMPLIANCES")
                        }
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <button
                  className={style.add_fees_head_btn}
                  title="Add Fees"
                  onClick={() => setStep("COMPLIANCES")}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  {t("add")}
                  <img src={`${assestsUserFinanceUrl}/plus.svg`} alt="add" />
                </button>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                      borderBottomColor: "rgba(18, 18, 18, 0.8)",
                      borderBottomWidth: "2px",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("net_pay")}</h6>
                    <h6
                      className={style.fees_total}
                      style={{
                        fontWeight: "700",
                      }}
                    >
                      {stateTotal.sal_com -
                        stateTotal.employee_ded -
                        stateTotal.com}
                    </h6>
                  </div>
                </div>
              </section>
              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("employer_deduction")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {state.employar_ded?.map((gfh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <input
                        type={"text"}
                        value={gfh?.head_name}
                        onChange={(e) =>
                          onFeeHeadLabelChange(e, index, "EMPLOYAR_DEDUCTION")
                        }
                        className={style.fee_head_name_input}
                      />
                      <input
                        type={"number"}
                        value={gfh?.head_amount}
                        onChange={(e) =>
                          onFeeHeadPriceChange(e, index, "EMPLOYAR_DEDUCTION")
                        }
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <button
                  className={style.add_fees_head_btn}
                  title="Add Fees"
                  onClick={() => setStep("EMPLOYAR_DEDUCTION")}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  {t("add")}
                  <img src={`${assestsUserFinanceUrl}/plus.svg`} alt="add" />
                </button>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                      borderBottomColor: "rgba(18, 18, 18, 0.8)",
                      borderBottomWidth: "2px",
                    }}
                  >
                    <h6 className={style.fees_total}>{t("ctc")}</h6>
                    <h6
                      className={style.fees_total}
                      style={{
                        fontWeight: "700",
                      }}
                    >
                      {stateTotal.sal_com -
                        stateTotal.employee_ded +
                        stateTotal.employar_ded}
                    </h6>
                  </div>
                </div>
              </section>

              <CreateButton label="add" onAction={onAddHeadPerson} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};
export default AddSalaryStructure;
