import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { usePayrollAllSalaryStructure } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import style from "../../../FinanceManager/FinanceManager.module.css";
import AddSalaryStructure from "../AddSalaryStructure/AddSalaryStructure";
import SalaryStructureCard from "../AddSalaryStructure/SalaryStructureCard";
const PayrollDefaultSalaryStructureGrant = ({ pid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);

  const {
    payrollAllSalaryStructure,
    payrollAllSalaryStructureLoading,
    payrollAllSalaryStructureRefetch,
  } = usePayrollAllSalaryStructure({
    data: {
      pid: pid,
      page: page,
      limit: 10,
      search: search,
      type: "ON_GRANT",
    },
    skip: !pid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (payrollAllSalaryStructure?.list?.length) {
      let obj = nextPage(payrollAllSalaryStructure?.list?.length);
      setState(() => obj);
    }
  }, [payrollAllSalaryStructure?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    payrollAllSalaryStructureRefetch();
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>

      {payrollAllSalaryStructure?.list?.map((struct, index) =>
        payrollAllSalaryStructure?.list?.length === index + 1 ? (
          <div key={struct?._id} ref={ref}>
            <SalaryStructureCard struct={struct} />
          </div>
        ) : (
          <SalaryStructureCard struct={struct} key={struct?._id} />
        )
      )}
      {payrollAllSalaryStructureLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        payrollAllSalaryStructure?.list?.length === 0 && (
          <EmptyMessage
            title="No salary structure found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        payrollAllSalaryStructure?.list?.length === 0 && (
          <EmptyMessage
            title="No salary structure."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      )}

      {add && (
        <AddSalaryStructure
          onClose={onToggle}
          onRefetch={onRefetch}
          pid={pid}
          flow="ON_GRANT"
        />
      )}
    </>
  );
};

export default PayrollDefaultSalaryStructureGrant;
